<template>
    <div class="views sail" data-enable="true">
        <!-- 助航信息 -->
        <div class="sail-info panel">
            <span>
                速度：
                <span>
                    <span>{{info.speed}}</span>
                    km/h
                </span>
            </span>
            <i class="line"></i>
            <span>
                极坐标：
                <span>
                    <span>{{info.polar}}</span>
                    度
                </span>
            </span>
        </div>
        <!-- 助航信息 -->

        <!-- 语音&文字按钮 -->
        <speech-button class="map-button shadow speech-button" v-model="speechStatus" @input="handlerSpeech"/>
        <word-button class="map-button shadow word-button" v-model="wordStatus"/>
        <!--<speech-gender-button class="map-button shadow speech-gender-button" v-model="wordStatus"/>-->
        <!-- 语音&文字按钮 -->

        <!-- 提示 -->
        <div class="sail-tip">航线仅供参考</div>
        <!-- 提示 -->

        <!-- 文字展示 -->
        <div class="word-wrapper shadow" v-if="wordStatus && word">
            <p class="words">{{word}}</p>
        </div>
        <!-- 文字展示 -->

        <!-- 助航按钮 -->
        <transition name="fade" mode="out-in">
            <div class="sail-control panel shadow" v-if="!firstClick">
                <mu-button class="button" color="#F5F5F5" @click="firstClick = true" flat>退出</mu-button>
                <div class="distance border-1px-l border-1px-r">
                    剩余 <span>{{info.distance | formatDistance}}</span>
                    {{Number.parseFloat(info.distance) > 1000 ? '公里' : '米'}}
                </div>
                <mu-button class="button" color="#F5F5F5" @click="handlerToggle(courseStatus)" flat>
                    {{courseStatus ? '正北' : '航向'}}
                </mu-button>
            </div>
            <div class="slider panel shadow" v-else>
                <mu-button class="button" color="#F5F5F5" @click="firstClick = false" flat>取消</mu-button>
                <mu-ripple class="slider-inner border-1px-l"
                           @click="retreat"
                           ref="slider">
                    退出助航
                </mu-ripple>
            </div>
        </transition>
        <!-- 助航按钮 -->
    </div>
</template>

<script>
    import {routeMixins, appMixins} from "../../common/mixins"
    import SpeechButton from '../../components/map/map-speech-button/map-speech-button'
    import WordButton from '../../components/map/map-word-button/map-word-button'
    import SpeechGenderButton from '../../components/map/sail-speech-gender-button/sail-speech-gender-button'

    export default {
        name: "sail",
        mixins: [routeMixins, appMixins],
        data() {
            return {
                info: {
                    speed: 0,
                    polar: 0,
                    distance: 0
                },
                word: '',
                speechStatus: true,
                wordStatus: true,
                courseStatus: true,

                // 动画
                firstClick: false
            }
        },
        created() {
            window.$types.map.navigation.setData = this.setData
            window.$types.map.navigation.setWord = this.setWord
            window.$types.map.navigation.close = this.close
        },
        activated() {
            this.firstClick = false

            if (JSON.stringify(this.$route.params) !== '{}') {
                this.info.speed = this.$route.params.speed
                this.info.polar = this.$route.params.polar
                this.info.distance = this.$route.params.distance
                this.word = this.$route.params.word
            }

            this.courseStatus = true

            this.speechStatus = this.speech
            this.wordStatus = this.text

            this.currentX = 0
            this.opacity = 0
            this.translateX = 0
        },
        methods: {
            /***
             * 退出助航
             */
            retreat() {
                this.resetRoutes()

                window.Arcgis && window.Arcgis.cancelPathPlanning('')

                this.$router.replace('/')
            },
            /***
             * 修改助航数据
             * @param info  JSON String
             */
            setData(info) {
                try {
                    info = JSON.parse(info)
                } catch (e) {
                    console.log(e)
                    info = {
                        speed: 0,
                        polar: 0,
                        distance: 0
                    }
                }

                this.info = info
            },
            /***
             * 助航结束
             */
            close(data) {
                try {
                    data = JSON.parse(data)
                } catch (err) {
                    console.log(err)

                    data = {}
                }

                this.$router.replace({
                    name: 'sail-over',
                    query: data
                })
            },
            /***
             * 助航状态下切换视角
             * @param value
             */
            handlerToggle(value) {
                const type = value ? 'trueNorth' : 'course'

                window.Arcgis && window.Arcgis.changeNavigationType(type, () => {
                    this.courseStatus = !value
                })
            },
            /***
             * 开关助航语音
             */
            handlerSpeech(value) {
                if (value) {
                    window.Arcgis && window.Arcgis.resumeBroadcast()
                } else {
                    window.Arcgis && window.Arcgis.pauseBroadcast()
                }
            },
            setWord(data) {
                this.word = data
            }
        },
        components: {
            SpeechButton, WordButton,
            SpeechGenderButton
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/mixins.styl"

    .sail
        .panel
            position absolute
            left 0
            right 0
            z-index 100
            display flex
            align-items center
            margin 10px 13px
            height 50px
            background-color rgba(0, 0, 0, .33)
            border-radius 6px
            overflow hidden
            transition all 0.3s
        .sail-info
            top 0
            padding 10px 13px
            > span
                flex 1
                font-size 14px
                text-align center
                color #F5F5F5
                > span
                    color #F5F5F5
                    > span
                        font-size 1.25em
            .line:before
                left-line(#f5f5f5)
        .slider
            bottom 0
            color #fff
            .button
                height 100%
            .slider-inner
                flex 1
                display flex
                align-items center
                justify-content center
                height 100%
                color #fff
                background-color transparent
        .sail-control
            bottom 0
            .distance
                flex 1
                text-align center
                color #F5F5F5
                > span
                    font-size 1.5em
            .button
                height 100%
        .speech-button
            top 110px
            left 13px
        .word-button
            top 70px
            left 13px
        .sail-tip
            position absolute
            top 50%
            right 13px
            padding 10px 8px
            width 30px
            line-height 1.8
            font-size 14px
            text-align center
            color #fff
            background-color rgba(0, 0, 0, .33)
            border-radius 5px
            transform translate3d(0, -50%, 0)
        .speech-gender-button
            top 150px
            left 13px
        .word-wrapper
            position absolute
            top 70px
            left 50px
            right 13px
            padding 0 5px
            line-height 30px
            max-height 90px
            background-color rgba(255, 255, 255, .9)
            border-radius 5px
            overflow-x hidden
            overflow-y auto
            .words
                line-height 30px
                transition all .5s
</style>



