<template>
    <div>
        <mu-checkbox class="speech-gender" color="#e91e63"
                     :input-value="speechGender === SPEECH_GENDER_WOMAN.value"
                     :value="SPEECH_GENDER_WOMAN.value"
                     uncheck-icon="sentiment_satisfied"
                     checked-icon="face"
                     @change="toggleSpeechGender"/>
    </div>
</template>

<script>
    import {appMixins} from "../../../common/mixins"

    export default {
        name: "sail-speech-gender-button",
        mixins: [appMixins],
        methods: {
            toggleSpeechGender(value) {
                value = value ? this.SPEECH_GENDER_WOMAN.value : this.SPEECH_GENDER_MAN.value

                this.setSpeechGender(value)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .speech-gender
        color #2e7bff
</style>



