<template>
    <div @click="click">
        <mu-icon :class="{'map-word-button__status': value}" value="translate"/>
    </div>
</template>

<script>
    export default {
        name: "map-word-button",
        props: {
            value: {}
        },
        methods: {
            click() {
                this.$emit('input', !this.value)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .map-word-button__status
        color #2e7bff
</style>



